<template>
  <div class="time">
    <canvas ref="canvasDom"></canvas>
    <span>当前跳动中小球个数 {{ballSize}} 个</span>
  </div>
</template>

<script>
import {
  NUM,
  LENGTH,
  RADIUS,
  TIME,
  SPEEDX,
  SPEEDY
} from 'config/timeConfig.js';

export default {
  name: 'times',
  data() {
    return {
      numList: [],
      playList: [],
      axisList: [],
      isAuto: false,
      ballLength: LENGTH,
      ballRadius: RADIUS,
      ballSize: 0
    };
  },
  methods: {
    // 初始化
    initNumberCanvas() {
      if (!this.$refs.canvasDom) return;
      this.canvas = this.$refs.canvasDom;
      let ctx = this.canvas.getContext('2d');
      this.canvas.width = this.canvas.offsetWidth;
      this.canvas.height = this.canvas.offsetHeight;
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        if (this.oldTime !== this.$dateFormat('hhmmss')) {
          let sec = new Date().getSeconds()
          this.isAuto = sec !== 0 && sec !== 30
          this.playList = this.playList.concat(this.axisList);
          this.ballSize = this.playList.length;
        }
        this.canvas.width = this.canvas.offsetWidth;
        this.canvas.height = this.canvas.offsetHeight;
        ctx.clearRect(0, 0, this.canvas.offsetWidth, this.canvas.offsetHeight);
        this.createAxis(ctx);
        this.play(ctx);
      }, TIME);
    },
    // 画圆
    drawCircle(x, y, ctx, color = 'red', r = this.ballRadius) {
      ctx.beginPath();
      ctx.fillStyle = color;
      ctx.arc(x, y, r, 0, 2 * Math.PI);
      ctx.fill();
      ctx.closePath();
    },
    // 下落函数
    running(obj) {
      obj.x += obj.vx;
      obj.y += obj.vy;
      obj.vy += obj.g;
      if (obj.y >= this.canvas.offsetHeight - this.ballRadius) {
        obj.y = this.canvas.offsetHeight - this.ballRadius;
        obj.vy = -obj.vy * 0.7;
      }
    },
    // 创建坐标数据
    createAxis(ctx) {
      this.axisList = [];
      let cen = this.center();
      let date = this.getDate();
      this.numList = [
        NUM[date[0]],
        NUM[date[1]],
        NUM[11],
        NUM[date[2]],
        NUM[date[3]],
        NUM[11],
        NUM[date[4]],
        NUM[date[5]]
      ];
      let maxLen = LENGTH * 8 * this.numList.length
      if (maxLen > this.canvas.width) {
        this.ballLength = this.canvas.width / this.numList.length / 8;
        this.ballRadius = this.ballLength / 3
      } else {
        this.ballLength = LENGTH;
        this.ballRadius = RADIUS
      }
      this.numList.forEach((item, itemIndex) => {
        item.forEach((child, childIndex) => {
          child.forEach((third, thirdIndex) => {
            if (third === 1) {
              this.axisList.push({
                x:
                  this.ballLength * thirdIndex +
                  this.ballLength * (child.length + 1) * itemIndex +
                  cen.left,
                y: childIndex * this.ballLength + cen.top,
                vx: this.randomSpeed().vx,
                vy: this.randomSpeed().vy,
                g: Math.random() * 2 + 2,
                color: this.randomColor()
              });
            }
          });
        });
      });
      this.axisList.forEach(item => {
        this.drawCircle(item.x, item.y, ctx);
      });
    },
    // 数字拆分单个
    getDate() {
      let date = new Date();
      let hour = date.getHours();
      let min = date.getMinutes();
      let sec = date.getSeconds();
      hour = hour < 10 ? `0${hour}` : hour.toString();
      min = min < 10 ? `0${min}` : min.toString();
      sec = sec < 10 ? `0${sec}` : sec.toString();
      let hourArr = hour.split('');
      let minArr = min.split('');
      let secArr = sec.split('');
      this.oldTime = `${hour}${min}${sec}`
      return hourArr.concat(minArr, secArr).map(item => item = Number(item));
    },
    // 数据跟新
    play(ctx) {
      this.playList.forEach(item => {
        this.running(item);
        this.drawCircle(item.x, item.y, ctx, item.color);
      });
      this.clear();
    },
    // 清楚超出的小球
    clear() {
      this.playList = this.playList.filter(item => {
        return item.x <= this.canvas.offsetWidth && item.x > 0;
      });
    },
    // 随机颜色
    randomColor() {
      const R = parseInt(Math.random() * 255);
      const G = parseInt(Math.random() * 255);
      const B = parseInt(Math.random() * 255);
      const A = 1;
      if (!this.isAuto) return 'rgba(255, 0, 0, 1)';
      return Math.random() > 0.2
        ? `rgba(${R},${G},${B},${A})`
        : `rgba(255,0,0,1)`;
    },
    // 随机速度
    randomSpeed() {
      let obj = {};
      let speedx = SPEEDX * Math.random() + SPEEDX / 2;
      obj.vx = Math.random() > 0.5 ? speedx : -speedx;
      obj.vy = Math.random() * SPEEDY;
      if (!this.isAuto) {
        obj.vx = SPEEDX;
        obj.vy = SPEEDY;
      }
      return obj;
    },
    // 居中
    center() {
      let len = this.numList.length;
      let width = this.canvas.offsetWidth;
      let height = this.canvas.offsetHeight;
      // 三个数字
      let numWidth = this.ballLength * NUM[0][0].length * len + this.ballLength * (len - 1);
      let numHeight = this.ballLength * 10;
      return {
        left: (width - numWidth) / 2,
        top: (height - numHeight) / 4
      };
    }
  },
  mounted() {
    this.initNumberCanvas();
    window.addEventListener('reset', this.initNumberCanvas, true)
  },
  destroyed() {
    clearInterval(this.timer)
    window.removeEventListener('reset', this.initNumberCanvas, true)
  }
}
</script>

<style lang="less" scoped>
.time {
  width: 100%;
  height: 100%;
  background-color: transparent;
  > canvas {
    width: 100%;
    height: 100%;
    background-color: transparent;
  }
  > span {
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
  }
}
</style>